import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Col, Row, Typography} from "antd";
import { FileDoneOutlined,FileImageOutlined,HomeOutlined,VideoCameraOutlined } from '@ant-design/icons'
import StatisticWidget from '../../../../components/shared-components/StatisticWidget/index'
import DataDisplayWidget from '../../../../components/shared-components/DataDisplayWidget/index'

const VillaCountingCard = connect(()=> {
    return {}
},{})(function(props){
    return (
        <div className="w-full">
            <Row gutter={16}>
                <Col xs={24} sm={24} md={24} lg={12} xl={12} xxl={12}>
                    <DataDisplayWidget
                        icon={<FileDoneOutlined />}
                        value={props?.data?.order ?? 0}
                        title="Total order"
                        color="cyan"
                        vertical={true}
                        avatarSize={55}
                    />
                    <DataDisplayWidget
                        icon={<HomeOutlined />}
                        value={props?.data?.room ?? 0}
                        title="Total Room"
                        color="gold"
                        vertical={true}
                        avatarSize={55}
                    />
                </Col>
                <Col xs={24} sm={24} md={24} lg={12} xl={12} xxl={12}>
                    <DataDisplayWidget
                        icon={<FileImageOutlined />}
                        value={props?.data?.photos ?? 0}
                        title="Total Photos"
                        color="blue"
                        vertical={true}
                        avatarSize={55}
                    />
                    <DataDisplayWidget
                        icon={<VideoCameraOutlined />}
                        value={props?.data?.videos ?? 0}
                        title="Total Videos"
                        color="volcano"
                        vertical={true}
                        avatarSize={55}
                    />
                </Col>
            </Row>
        </div>
    )
})
VillaCountingCard.propTypes= {
    villaId:PropTypes.string.isRequired
}
VillaCountingCard.defaultProps = {
    villaId:null
}
export default VillaCountingCard