import React,{useState,useEffect} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Button, Table, Avatar} from "antd";
import PropsDefault from "../../../../constants/props-default";
import {Link, useParams} from "react-router-dom";
import ApiService from "../../../../services/Api.service";
import Utils from "../../../../utils";
import moment from "moment/moment";


const VillaDetailTransaction = connect(()=> {
    return {}
},{})(function(props){

    const param = useParams()
    const [params,setParams] = useState({
        limit:3,
        page:1,
    })
    const [ loading,setLoading]= useState(false)
    const [ data,setData] = useState({})

    useEffect(()=> {
        setLoading(true)
        setData({
            ...data,
            villa:{
                ...data?.villa
            },
            order:[]
        })
        new ApiService({
            url:`/api/v2/villa/${param?.id}/transaction`,
            config :{
                params: {
                    ...params
                }
            }
        }).get()
            .then((response)=> {
                setData({
                    ...response?.data,
                    order: [...response?.data?.order]
                })
                setLoading(false)
                console.log({response})
            })
            .catch((err)=> {
                setLoading(false)
                setData({
                    ...data,
                    villa:{
                        ...data?.villa
                    },
                    order:[]
                })
                console.log({err})
            })
    },[params])

    const columns = [
        {
            dataIndex: "_id",
            title:"No",
            render:(_,val,index)=> index + 1
        },
        {
            dataIndex: "room",
            title:"Room",
            render:(_)=> {
                return(
                    <div className="w-full text-left">
                        <Avatar src={null} className={'mr-2'}/>
                        <span>{_?.name ?? "-"}</span>
                    </div>
                )
            }
        },
        {
            dataIndex: "order_number",
            title:"Order Number"
        },
        {
            dataIndex:"status",
            title:"Status"
        },
        {
            dataIndex: "amount",
            title:"Amount",
            render:(_)=> Utils.numberFormat(_,',')
        }
    ]
    const ExtraComponent = ()=> {
        return(
            <div className="text-right">
                <Link to={`${props?.villaId}/transaction`}>
                    <Button size={'small'} type={'primary'} ghost>Detail</Button>
                </Link>

            </div>
        )
    }
    return(
        <Card title={props?.card?.title ?? 'Transaction'} bordered={props?.card?.bordered ?? false} extra={props?.card?.extra ?? <ExtraComponent/>}>
            <Table
                loading={loading}
                dataSource={data?.order ?? []}
                columns={columns}
            />
        </Card>
    )
})

VillaDetailTransaction.propTypes = {
    ...PropsDefault.types.card,
    villaId: PropTypes.string.isRequired
}
VillaDetailTransaction.defaultProps = {
    ...PropsDefault.default.card({
        title:"Transaction",
        extra:null
    }),
    villaId: null
}


export default VillaDetailTransaction