import React,{ useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Row, Col, Avatar, Button, Tabs} from 'antd'
import PropsDefault from "../../../../constants/props-default";
import ApiService from "../../../../services/Api.service";
import {useParams} from "react-router-dom";
import {PageHeaderAlt} from "../../../../components/layout-components/PageHeaderAlt";
import Flex from "../../../../components/shared-components/Flex";

const VillaDetailInformation = connect(()=> {
    return {}
},{})(function(props){
    let  {card} = props
    const param = useParams()
    const [params,setParams]= useState({})
    const [loading,setLoading] = useState(false)
    const [data,setData] =useState({})

    useEffect(()=> {
        setLoading(true)
        new ApiService({
            url:`/api/v2/villa/${param?.id}`
        }).get()
            .then((response)=> {
                setLoading(false)
                setData({...response?.data})
            })
            .catch((err)=> {
                setLoading(false)
            })
    },[])
    console.log(data,'INFORMATION')
    return (
        <>
            <div className="my-4">
                <Card>
                    <Row justify="center">
                        <Col sm={24} md={23}>
                            <div className="d-md-flex">
                                <div className="rounded p-2 bg-white shadow-sm mx-auto" style={{'marginTop': '-3.5rem', 'maxWidth': `${150 + 16}px`}}>
                                    <Avatar shape="square" size={150} src={[data?.thumbnail?.prefix_url,data?.thumbnail?.filename].join("/")} />
                                </div>
                                <div className="ml-md-4 w-100">
                                    <Flex alignItems="center" mobileFlex={false} className="mb-3 text-md-left text-center">
                                        <h2 className="mb-0 mt-md-0 mt-2">{data?.name ?? "-"}</h2>
                                        <div className="ml-md-3 mt-3 mt-md-0">
                                            <Button size="small" type="primary">Follow</Button>
                                            <Button size="small" className="ml-2">Message</Button>
                                        </div>
                                    </Flex>
                                    <Row gutter="16">
                                        <Col sm={24} md={8}>
                                            <p className="mt-0 mr-3 text-muted text-md-left text-center">
                                                {data?.bio ?? ""}
                                            </p>
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Card>
                <div className="">
                    <Row gutter={24}>
                        <Col lg={6} xs={24}>
                            {props?.extra ?? ""}
                        </Col>
                        <Col lg={18} xs={24}>
                            <Card>
                                <Tabs>
                                    <Tabs.TabPane tab={'Information'} key={'information'}>
                                        <p>testing</p>
                                    </Tabs.TabPane>
                                    <Tabs.TabPane tab={'Photos'} key={'photos'}>
                                        <p>testing</p>
                                    </Tabs.TabPane>
                                    <Tabs.TabPane tab={'Videos'} key={'videos'}>
                                        <p>Videos</p>
                                    </Tabs.TabPane>
                                </Tabs>
                            </Card>
                            {props?.bottom ?? ""}
                        </Col>

                    </Row>

                </div>
            </div>
        </>
    )
})

VillaDetailInformation.propTypes = {
    ...PropsDefault.types.card
}
VillaDetailInformation.defaultProps = {
    ...PropsDefault.default.card({
        title:"Information"
    })
}
export default VillaDetailInformation