import React, { useState,useEffect} from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Card, Row, Col, } from 'antd';
import VillaCountingCard from "./villa-counting-card";
import VillaDetailTransaction from "./villa-detail-transaction";
import VillaDetailInformation from "./villa-detail-information";
import {useParams} from "react-router-dom";
import ApiService from "../../../../services/Api.service";

export const VillaDetailPage = connect(()=> {
    return {}
},{})(function(props){
    const param = useParams()

    const [params,setParams]= useState({
        order_limit:10,
        order_page:1
    })
    const [loading,setLoading] = useState(false)
    const [data,setData] = useState({})
    useEffect(()=> {
        setLoading(true)
        new ApiService({
            url:`/api/v2/villa/${param?.id}/detail-page`,
            config :{
                params :{
                    ...params
                }
            }
        }).get()
            .then((response)=> {
                console.log({response})
                setLoading(false)
                setData({...response?.data})

            })
            .catch((err)=> {
                console.log({err})
            })
    },[params])
    console.log({data})
    return (
        <div className="w-full">


            <Row gutter={24}>
                <Col
                    xs={24}
                >
                    <VillaDetailInformation
                        villaId={param?.id ?? null}
                        extra={<VillaCountingCard villaId={param?.id} data={data?.count ?? {}} loading={loading}/>}
                        bottom={
                            <VillaDetailTransaction villaId={param?.id ?? null}/>
                        }
                    />
                </Col>
            </Row>
        </div>
    )
})

VillaDetailPage.propTypes = {}
VillaDetailPage.defaultProps = {}
export default VillaDetailPage